import { openDB } from 'idb'
import { v4 as uuidv4 } from 'uuid'
import dayjs from 'dayjs'

const DB_NAME = 'tasks_store'
const DB_VERSION = 1
const TASK_STORE_NAME = 'tasks'

class Task {
  constructor() {
    this.dbPromise = openDB(DB_NAME, DB_VERSION, {
      upgrade(db) {
        db.createObjectStore(TASK_STORE_NAME, { keyPath: 'id' })
      },
    })
  }

  async create(task) {
    const id = uuidv4()

    const taskToCreate = {}
    taskToCreate.id = id
    taskToCreate.title = task.title ? task.title : 'Sin titulo'
    taskToCreate.description = task.description ? task.description : ''
    taskToCreate.haveAditionalTasks = task.haveAditionalTasks ? task.haveAditionalTasks : false
    taskToCreate.aditionalTasks = task.aditionalTasks ? task.aditionalTasks : [] 
    taskToCreate.completed = false
    taskToCreate.date = dayjs()

    const db = await this.dbPromise
    const tx = db.transaction(TASK_STORE_NAME, 'readwrite')
    const store = tx.objectStore(TASK_STORE_NAME)
    await store.add(taskToCreate)
    await tx.complete
  }

  async read(id) {
    const db = await this.dbPromise
    const tx = db.transaction(TASK_STORE_NAME, 'readonly')
    const store = tx.objectStore(TASK_STORE_NAME)
    const photo = await store.get(id)
    await tx.complete
    return photo
  }


  async update(task) {
    const taskToUpdate = {}
    taskToUpdate.id = task.id
    taskToUpdate.title = task.title ? task.title : 'Sin titulo'
    taskToUpdate.description = task.description ? task.description : ''
    taskToUpdate.haveAditionalTasks = task.haveAditionalTasks ? task.haveAditionalTasks : false
    taskToUpdate.aditionalTasks = task.aditionalTasks ? task.aditionalTasks : [] 
    taskToUpdate.completed = task.completed ? task.completed : false

    const db = await this.dbPromise
    const tx = db.transaction(TASK_STORE_NAME, 'readwrite')
    const store = tx.objectStore(TASK_STORE_NAME)
    await store.put(taskToUpdate)
    await tx.complete
  }

  async delete(id) {
    const db = await this.dbPromise
    const tx = db.transaction(TASK_STORE_NAME, 'readwrite')
    const store = tx.objectStore(TASK_STORE_NAME)
    await store.delete(id)
    await tx.complete
  }

  async getAll() {
    const db = await this.dbPromise
    const tx = db.transaction(TASK_STORE_NAME, 'readonly')
    const store = tx.objectStore(TASK_STORE_NAME)
    const tasks = await store.getAll()
    await tx.complete
    return tasks
  }

  async getIncompleteTasks() {
    const db = await this.dbPromise
    const tx = db.transaction(TASK_STORE_NAME, 'readonly')
    const store = tx.objectStore(TASK_STORE_NAME)
  
    const currentDate = dayjs()
    const incompleteTasks = []
  
    const index = store.index('completed')
    const range = IDBKeyRange.only(false)
  
    const cursorPromise = new Promise((resolve, reject) => {
      const cursorRequest = index.openCursor(range)
      cursorRequest.onsuccess = (event) => {
        const cursor = event.target.result
        if (cursor) {
          const task = cursor.value
          const taskDate = dayjs(task.date)
          if (taskDate.isBefore(currentDate, 'day')) {
            incompleteTasks.push(task)
          }
          cursor.continue()
        } else {
          resolve()
        }
      }
      cursorRequest.onerror = reject
    })
    await cursorPromise
  
    await tx.complete
    return incompleteTasks
  }
  
}

export default new Task()