<template>
    <v-row class="ma-0 pa-0" align="center" justify="center">
        <v-col cols="12" align="center" align-self="center">
            <v-progress-linear color="primary" height="25" striped :value="progress">
                <template v-slot:default="{ value }">
                    <strong>{{ Math.ceil(value) }}%</strong>
                </template>
            </v-progress-linear>
        </v-col>
        <v-col :cols="$vuetify.breakpoint.smAndDown ? 6 : 10" align="center" align-self="center">
            <v-btn text block @click="taskInfoState = true">{{ task.title }}</v-btn>
            <v-dialog v-model="taskInfoState" max-width="500">
                <v-card color="primary">
                    <v-row class="ma-0 pa-0">
                        <v-col cols="10" align-self="center">
                            <v-card-title>{{ task.title }}</v-card-title>
                        </v-col>
                        <v-col cols="2" align-self="start">
                            <v-btn icon color="white" @click="taskInfoState = false">
                                <v-icon>close</v-icon>
                            </v-btn>
                        </v-col>
                        <v-col cols="12">
                            <v-progress-linear color="black" striped height="25" :value="progress">
                                <template v-slot:default="{ value }">
                                    <strong>{{ Math.ceil(value) }}%</strong>
                                </template>
                            </v-progress-linear>
                        </v-col>
                    </v-row>
                    <v-card-text>
                        <v-divider />
                        <span>{{ task.description }}</span>
                        <template v-if="task.haveAditionalTasks">
                            <v-row class="ma-0 pa-0" align="center" justify="center" v-for="(subtask, index) in task.aditionalTasks" :key="index">
                                <v-col cols="10" align-self="center">
                                    {{ subtask.title }}
                                </v-col>
                                <v-col cols="2" align-self="center">
                                    <v-checkbox color="white" v-model="task.aditionalTasks[index].complete"></v-checkbox>
                                </v-col>
                            </v-row>
                        </template>
                    </v-card-text>
                </v-card>
            </v-dialog>
        </v-col>
        <v-col :cols="$vuetify.breakpoint.smAndDown ? 6 : 2" align-self="center">
            <v-row align="center" justify="center" class="ma-0 pa-0">
                <v-col cols="4" align-self="center">
                    <v-btn icon @click="deleteTask = true">
                        <v-icon>delete</v-icon>
                    </v-btn>
                    <template v-if="deleteTask">
                        <delete-task @delete-task="$emit('delete-task', task)" v-model="deleteTask"/>
                    </template>
                </v-col>
                <v-col cols="4" align-self="center">
                    <v-btn icon @click="editTask = true">
                        <v-icon>edit</v-icon>
                    </v-btn>
                    <template v-if="editTask">
                        <edit-task @input="inputEditTask" v-model="task" :open="editTask" @close="editTask = false"/>
                    </template>
                </v-col>
                <v-col cols="4" align-self="center">
                    <v-checkbox color="success" v-model="task.completed"></v-checkbox>
                </v-col>
            </v-row>
        </v-col>
    </v-row>
</template>

<script>
import EditTask from './EditTask.vue'
import DeleteTask from './DeleteTask.vue'

export default {
    name: 'task-admin',
    components: {
        EditTask,
        DeleteTask
    },
    props: {
        value: {
            type: Object,
            required: true
        }
    },
    data(){
        return{
            taskInfoState: false,
            editTask: false,
            deleteTask: false
        }
    },
    methods: {
        inputEditTask(task){
            this.$emit('input', task)
        }
    },
    computed:{
        task:{
            get(){
                return this.value
            },
            set(value){
                console.log('setted')
                this.$emit('input', value)
            }
        },
        isChecked(){
            return this.progress == 100
        },
        progress(){
            if(this.task.haveAditionalTasks){
                const tasksLength = this.task.aditionalTasks.length
                let tasksCompleted = 0
                this.task.aditionalTasks.forEach(task => {
                    if(task.complete) tasksCompleted ++
                })
                if(tasksCompleted === 0) return 0
                return tasksCompleted * 100 / tasksLength
            }
            
            return this.task.completed ? 100 : 0 
        }
    },
    watch:{
        'task.completed': {
            handler(value){
                if(value){
                    this.task.aditionalTaks = this.task.aditionalTasks.map(task => {
                        task.complete = true
                        return task
                    })
                }
                this.$emit('input', this.task)
            }
        },
        'progress': {
            handler(){
                this.task.aditionalTasks.forEach(task => {
                    if(!task.complete) this.task.completed = false
                })
                this.$emit('input', this.task)
            }
        }
    }
}
</script>

