<template>
  <v-dialog v-model="formState" max-width="500" persistent>
    <v-card max-width="500">
        <v-row class="pa-0 ma-0">
            <v-col cols="10">
                <v-card-title>Ingresar una nueva tarea</v-card-title>
            </v-col>
            <v-col cols="2" align="end">
                <v-btn color="error" large icon @click="$emit('input', false)">
                    <v-icon>cancel</v-icon>
                </v-btn>
            </v-col>
            <v-card-text>
                <v-text-field 
                    append-icon="assignment"
                    label="Titulo" 
                    counter="60" 
                    :rules="[v => v.length <= 60] || 'El máximo permitido son 30 caracteres.'" 
                    hint="Titulo de la tarea"
                    v-model="title"
                />
                <v-textarea 
                    append-icon="description"
                    label="Descripción"
                    rows="2"
                    counter="2000"
                    :rules="[v => v.length <= 60] || 'El máximo permitido son 2000 caracteres.'"
                    v-model="description"
                />
                <v-checkbox
                    label="¿Quieres añadir subtareas?"
                    v-model="haveAditionalTasks"
                ></v-checkbox>
                <template v-if="haveAditionalTasks">
                    <v-row v-for="(task, index) of aditionalTasks" :key="index" class="ma-0 pa-0">
                        <v-col cols="10">
                            <v-text-field append-icon="check_circle" v-model="aditionalTasks[index].title"></v-text-field>
                        </v-col>
                        <v-col cols="2" align="center" align-self="center">
                            <v-btn large icon color="error" @click="aditionalTasks.splice(index, 1)"><v-icon>delete</v-icon></v-btn>
                        </v-col>
                    </v-row>
                    <v-card-actions>
                        <v-btn block color="primary" @click="aditionalTasks.push({complete: false})">
                            <v-icon left>add</v-icon>Añadir una nueva tarea
                        </v-btn>
                    </v-card-actions>
                </template>
                <v-card-actions>
                    <v-btn block color="primary" @click="saveTask" :loading="saving">
                        <v-icon left>save</v-icon>Guardar Tarea
                    </v-btn>
                </v-card-actions>
            </v-card-text>
        </v-row>
    </v-card>
  </v-dialog>
</template>

<script>
import task from '../models/Task'

export default {
    name: 'new-task-form',
    props: {
        value: {
            type: Boolean,
            required: true
        }
    },
    computed: {
        formState: {
            get(){
                return this.value
            },
            set(value){
                this.$emit('input', value)
            }
        }
    },
    data(){
        return {
            saving: false,
            title: '',
            description: '',
            haveAditionalTasks: false,
            aditionalTasks: []
        }
    },
    methods: {
        async saveTask(){
            this.saving = true
            try {
                await task.create({
                    title: this.title,
                    description: this.description,
                    haveAditionalTasks: this.haveAditionalTasks,
                    aditionalTasks: this.aditionalTasks
                })
                this.$emit('new-task-added')
            } catch (error) {
                console.error('error: ', error.message)
            } finally {
                this.saving = false
                this.$emit('input', false)
            }
        }
    }
}
</script>

