<template>
  <v-dialog v-model="dialog" persistent max-width="500">
    <v-card>
        <v-card-title>¿Estas seguro de eliminar la tarea?</v-card-title>
        <v-card-text>
            <v-divider />
            Una vez eliminada la tarea no podrá recuperarse.
        </v-card-text>
        <v-card-actions>
            <v-row class="pa-0 ma-0">
                <v-col cols="12" sm="6">
                    <v-btn block color="error" @click="deleteTask">si</v-btn>
                </v-col>
                <v-col cols="12" sm="6">
                    <v-btn block color="blue" @click="$emit('input', false)">No</v-btn>
                </v-col>
            </v-row>
        </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
    name: 'delete-task',
    props: {
        value: {
            type: Boolean,
            required: true
        }
    },
    computed:{
        dialog:{
            get(){
                return this.value
            },
            set(value){
                this.$emit('input', value)
            }
        }
    },
    methods: {
        deleteTask(){
            this.$emit('delete-task')
            this.$emit('input', false)
        }
    }
}
</script>

