<template>
  <v-dialog v-model="dialog" max-width="500" persistent>
    <v-card>
        <v-card-title>Edición de tarea</v-card-title>
        <v-card-text>
            <v-divider />
            <v-text-field 
                append-icon="assignment"
                label="Titulo" 
                counter="60" 
                :rules="[v => v.length <= 60] || 'El máximo permitido son 30 caracteres.'" 
                hint="Titulo de la tarea"
                v-model="title"
            />
            <v-textarea 
                append-icon="description"
                label="Descripción"
                rows="2"
                counter="2000"
                :rules="[v => v.length <= 60] || 'El máximo permitido son 2000 caracteres.'"
                v-model="description"
            />
            <v-checkbox
                label="¿Quieres añadir subtareas?"
                v-model="haveAditionalTasks"
            ></v-checkbox>
            <template v-if="haveAditionalTasks">
                <v-row v-for="(task, index) of aditionalTasks" :key="index" class="ma-0 pa-0">
                    <v-col cols="10">
                        <v-text-field append-icon="check_circle" v-model="aditionalTasks[index].title"></v-text-field>
                    </v-col>
                    <v-col cols="2" align="center" align-self="center">
                        <v-btn large icon color="error" @click="aditionalTasks.splice(index, 1)"><v-icon>delete</v-icon></v-btn>
                    </v-col>
                </v-row>
                <v-card-actions>
                    <v-row class="ma-0 pa-0">
                        <v-col cols="12">
                            <v-btn block color="primary" @click="aditionalTasks.push({complete: false})">
                                <v-icon left>add</v-icon>Añadir una nueva tarea
                            </v-btn>
                        </v-col>
                    </v-row>
                </v-card-actions>
            </template>
            <v-card-actions>
                <v-row align="center" justify="center" class="ma-0 pa-0">
                    <v-col align-self="center" cols="12" sm="6">
                        <v-btn block color="primary" @click="save">
                            <v-icon left>save</v-icon>Guardar cambios
                        </v-btn>
                    </v-col>
                    <v-col align-self="center" cols="12" sm="6">
                        <v-btn block color="error" @click="cancel">
                            <v-icon left>cancel</v-icon>Cancelar
                        </v-btn>
                    </v-col>
                </v-row>
            </v-card-actions>
        </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
    name: 'edit-task',
    props: {
        value: {
            type: Object,
            required: true
        },
        open: {
            type: Boolean,
            required: true
        }
    },
    data(){
        return {
            title: this.value.title,
            description: this.value.description,
            haveAditionalTasks: this.value.haveAditionalTasks,
            aditionalTasks: this.value.aditionalTasks ? this.value.aditionalTasks.map(e => {
                return {
                    complete: e.complete,
                    title: e.title
                }
            }) : []
        }
    },
    computed: {
        dialog: {
            get(){
                return this.open
            },
            set(value){
                if(!value) this.$emit('close')
            }
        }
    },
    methods:{
        save(){
            this.$emit('input', {
                title: this.title,
                description: this.description,
                aditionalTasks: this.aditionalTasks,
                completed: this.value.completed,
                haveAditionalTasks: this.haveAditionalTasks,
                id: this.value.id
            })
            this.$emit('close')
        },
        cancel(){
            this.$emit('close')
        }
    }
}
</script>

