<template>
  <v-card>
    <v-card-title>¿Listo para el día de hoy? Estas son tus tareas pendientes de ayer...</v-card-title>
    <v-card-text v-if="tasks.length > 0">
        <v-divider />
        <v-card class="my-4" v-for="(task, index) in tasks" :key="task.id">
            <task v-model="tasks[index]" @input="task => inputTask(index, task)" @delete-task="deleteTask(index, task)"/>
        </v-card>
        <v-card-actions>
            <v-btn color="primary" block >
                Finalizar el día<v-icon right>check_circle</v-icon>
            </v-btn>
        </v-card-actions>
    </v-card-text>
    <v-card-text v-else>
        No hay tareas pendientes para hoy
    </v-card-text>
  </v-card> 
</template>

<script>
import task from '../models/Task'
import Task from './Task.vue'

export default {
    name: 'tasks-viewer',
    components: {
        Task
    },
    props: {
        newTask: {
            type: Boolean,
            required: true
        }
    },
    data(){
        return {
            tasks: []
        }
    },
    watch:{
        newTask(value){
            console.log('change: ', value)
            if(value){
                this.fetchTasks()
                this.$emit('task-added')
            }
        }
    },
    created(){
        this.fetchTasks()
    },
    methods: {
        async fetchTasks(){
            this.tasks = await task.getAll()
        },
        async inputTask(index, taskToUpdate){
            this.tasks[index] = taskToUpdate
            await task.update(taskToUpdate)
        },
        async deleteTask(index, taskToDelete){
            this.tasks.splice(index, 1)
            await task.delete(taskToDelete.id)
        }
    }
}
</script>

